import React, { FC, HTMLAttributes, useEffect, useState } from 'react';
import Carousel from "react-spring-3d-carousel";
import { config } from "react-spring";

//styles
import './styles.scss';
//components
import { PartnerCard } from './Card';
//img
import { 
    partner1,
    partner2,
    partner3,
    partner4,
    partner5,
} from '../../assets/img';

interface PartnerCarouselProps extends HTMLAttributes<HTMLDivElement> {}

const cards = [
    { key: 1, img: partner1, title: "Chiropractic Fairview" },
    { key: 2, img: partner2, title: "A Place At Home" },
    { key: 3, img: partner3, title: "A/C Works" },
    { key: 4, img: partner4, title: "Kings Roofling LLC" },
    { key: 5, img: partner5, title: "Accasia Event Venue" },
]

export const PartnerCarousel: FC<PartnerCarouselProps> = () => {
    const [goToSlide, setGoToSlide] = useState<number>(0);

    const table = cards.map((item, index) => {
        return { 
            ...item, 
            content: <PartnerCard imagen={item.img} title={item.title}/>,
            onClick: () => setGoToSlide(index) 
        };
    });

    return (
        <div className='justify-center'>
            <div className='partnerCarousel'>
                <Carousel
                    slides={table}
                    goToSlide={goToSlide}
                    offsetRadius={2}
                    showNavigation={false}
                    animationConfig={config.gentle}
                />
            </div>
        </div>
    );
};
