import React, { FC, HTMLAttributes, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons'

//styles
import './styles.scss';
//img
import { logo } from '../../../assets/img';
//constants
import { ROUTES } from '../../../constants';

interface HomeHeaderProps extends HTMLAttributes<HTMLDivElement> {}

export const HomeHeader: FC<HomeHeaderProps> = () => {
  const [mobileMenuClick, setMobileMenuClick] = useState<boolean>(false)

  const showMenuItem = () => {
    setMobileMenuClick(true)
  }

  const hideMenuItem = () => {
    setMobileMenuClick(false)
  }

  return (
    <div className={'homeHeader'}>
      <div className='homeHeader__container align-center'>
        <Link to={ROUTES.home}>
          <div className='logo mr48'></div>
        </Link>
        <div className='homeHeader__containerLink ml48'>
          <Link to={ROUTES.event}>
            <span className='homeHeader__containerTitle'>Events</span>
          </Link>
          <Link to={ROUTES.partner}>
            <span className='homeHeader__containerTitle'>Partners</span>
          </Link>
          <Link to={ROUTES.resource}>
            <span className='homeHeader__containerTitle'>Resources</span>
          </Link>
        </div>
        <div className='homeHeader__mMenuItem'>
          <label htmlFor="chk" className="show-menu-btn" onClick={() => showMenuItem()}>
            <HamburgerIcon boxSize={35}/>
          </label>
        </div>
      </div>
      <ul className={mobileMenuClick ? `menu mobile-menu-show` : 'menu'}>
        <Link to={ROUTES.event} onClick={() => hideMenuItem()}>Events</Link>
        <Link to={ROUTES.partner} onClick={() => hideMenuItem()}>Partners</Link>
        <Link to={ROUTES.resource} onClick={() => hideMenuItem()}>Resources</Link>
        <label htmlFor="chk" className="hide-menu-btn" onClick={() => hideMenuItem()}>
          <CloseIcon boxSize={25}/>
        </label>
      </ul>
    </div>
  );
};
