import React, { FC, HTMLAttributes, useEffect, useState } from 'react';
import { useSpring, animated } from "react-spring";

//styles
import './card.scss';
//img
import { 
    carousel1,
    carousel2,
    carousel3,
    carousel4,
    carousel5,
} from '../../assets/img';

interface PartnerCardProps extends HTMLAttributes<HTMLDivElement> {
    imagen: string;
    title: string;
}

export const PartnerCard: FC<PartnerCardProps> = ({imagen, title}) => {
    const [show, setShown] = useState<boolean>(false);

    const props3 = useSpring({
        transform: show ? "scale(1.03)" : "scale(1)",
        boxShadow: show
            ? "0 20px 25px rgb(0 0 0 / 25%)"
            : "0 2px 10px rgb(0 0 0 / 8%)"
        });
    return (
        <animated.div
            className="partnerCard"
            style={props3}
            onMouseEnter={() => setShown(true)}
            onMouseLeave={() => setShown(false)}
        >
            <img className='partnerCard__img' src={imagen} alt="" />
            <div className='partnerCard__general'>
                <h2 className='partnerCard__title'>{title}</h2>
                <p className='partnerCard__text'>
                    Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam
                    nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat
                    volutpat.
                </p>
            </div>
        </animated.div>
    );
};
